import React from 'react';
import { makeStyles } from '@material-ui/core';
import Typography from '../components/Typography';
import FlexBox from '../components/FlexBox';
import ImageCard from '../components/ImageCard';
import servicesPageImage1 from '../media/servicesPage1.png';
import servicesPageImage2 from '../media/servicesPage2.png';
import servicesPageImage3 from '../media/servicesPage3.png';
import NavLeftRight from '../components/NavLeftRight';
import { PageRoutes, ServicesPageRoutes } from '../models/routes';
import { useHistory } from 'react-router-dom';
import { IS_TOUCH_SCREEN } from '../utils/constants';

const useStyles = makeStyles((theme) => ({
  titleLabel: {
    textTransform: 'uppercase',
    fontWeight: 600,
  },
  labelText: {
    textTransform: 'uppercase',
    fontWeight: 600,
    padding: theme.spacing(1),
    borderRadius: '4px',
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    background: 'rgb(255 255 255)',
    whiteSpace: 'pre',
  },
  imageCardsContainer: {
    justifyContent: 'space-between',
    [theme.breakpoints.down('xs')]: {
      flexDirection: 'column',
    },
  },
  titleAndDescriptionContainer: {
    flexDirection: 'column',
    margin: `${theme.spacing(2)}px`,
  },
  descriptionText: {
    fontFamily: 'Poppins',
  },
  imageContainer: {
    '&:hover > img': {
      filter: 'none',
    },
    '&:hover > p': {
      background: 'rgb(255 255 255 / 80%)',
    },
  },
  media: {
    height: 340,
    filter: IS_TOUCH_SCREEN ? 'none' : 'grayscale(100%)',
  },
  imageOverlay: {
    position: 'absolute',
    top: '50%',
    translate: '-50%, -50%',
    '&:hover > p': {
      background: 'rgb(255 255 255 / 66%)',
    },
  },
}));

const ServicesPage = () => {
  const classes = useStyles();
  const history = useHistory();
  return (
    <FlexBox flexDirection='column' >
      <FlexBox className={classes.titleAndDescriptionContainer}>
        <Typography header variant='h4' className={classes.titleLabel}>
          Services
        </Typography>
        <Typography className={classes.descriptionText}>
          Ty Karges Home is a home staging and interior design firm located in San Jose, California
      </Typography>
      </FlexBox>
      <FlexBox className={classes.imageCardsContainer}>
        <ImageCard image={servicesPageImage1} buttonClass={classes.imageContainer} className={classes.media} onClick={() => history.push(ServicesPageRoutes.HomeStaging)}>
          <Typography header className={classes.labelText}>
            Home Staging
          </Typography>
        </ImageCard>
        <ImageCard image={servicesPageImage2} buttonClass={classes.imageContainer} className={classes.media} onClick={() => history.push(ServicesPageRoutes.PreSale)}>
          <Typography header className={classes.labelText}>
            {`Pre-Sale\nHome Facelift`}
          </Typography>
        </ImageCard>
        <ImageCard image={servicesPageImage3} buttonClass={classes.imageContainer} className={classes.media} onClick={() => history.push(ServicesPageRoutes.InteriorDesign)}>
          <Typography header className={classes.labelText}>
            Interior Design
          </Typography>
        </ImageCard>
      </FlexBox>
      <NavLeftRight
        left={{ label: 'View Portfolio', href: PageRoutes.Portfolio }}
        right={{ label: 'Contact Us', href: PageRoutes.Contact }}
      />
    </FlexBox>
  );
};

export default ServicesPage;