import React from 'react';
import { makeStyles, Grid, TextField, Button } from '@material-ui/core';
import Typography from '../components/Typography';
import { Formik } from 'formik';
import FlexBox from '../components/FlexBox';
import contactPageImage1 from '../media/contactPage1.png';
import { ReactComponent as LongArrowRight } from '../media/longArrowRight.svg';

const useStyles = makeStyles((theme) => ({
  image: {
    width: '100%',
  },
  formContainer: {
    background: '#222223',
    zIndex: 1,
    padding: theme.spacing(3),
    maxWidth: 580,
  },
  contactContentContainer: {
    position: 'relative',
    background: `url(${contactPageImage1}) no-repeat center center / cover`,
    padding: '115px 15px',
    width: '100%',
    justifyContent: 'center',
  },
  textInput: {
    '& *': {
      color: 'white',
    },
    '& .MuiInput-underline:before': {
      borderColor: 'white',
    },
    '& .MuiInput-underline:hover:not(.Mui-disabled):before': {
      borderColor: 'white',
    },
    '& label': {
      fontFamily: 'AngelicBonques',
      textTransform: 'uppercase',
      fontWeight: 'bold',
    },
  },
  whiteTextColor: {
    color: 'white',
    fontWeight: 'bold',
  },
  arrowSvg: {
    fill: 'white',
    height: '1em',
    marginLeft: theme.spacing(3),
  },
}));

const ContactPage = () => {
  const classes = useStyles();
  return (
    <FlexBox className={classes.contactContentContainer}>
      {/* <img src={contactPageImage1} className={classes.image} alt='Workdesk with coffee and laptop' /> */}
      <Formik
        initialValues={{ name: '', email: '', phone: '', message: '' }}
        validate={values => {
          const errors = {} as Record<keyof typeof values, string>;
          if (!values.email) {
            errors.email = 'Required';
          } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
          ) {
            errors.email = 'Invalid email address';
          }
          return errors;
        }}
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            setSubmitting(false);
          }, 400);
        }}
      >
        {(props) => (
          <FlexBox flexDirection='column' className={classes.formContainer}>
            <Typography header variant='h4' className={classes.whiteTextColor}>
              Contact
            </Typography>
            <Grid container spacing={4}>
              <Grid item xs={12}>
                <TextField className={classes.textInput} fullWidth label='Name' value={props.values.name} onChange={props.handleChange} name='name' />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField className={classes.textInput} fullWidth label='Email' value={props.values.email} onChange={props.handleChange} name='email' type='email' />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField className={classes.textInput} fullWidth label='Phone' value={props.values.phone} onChange={props.handleChange} name='phone' type='tel' />
              </Grid>
              <Grid item xs={12}>
                <TextField className={classes.textInput} fullWidth multiline label='Message' value={props.values.message} onChange={props.handleChange} name='message' />
              </Grid>
              <Grid item xs={12}>
                <Button fullWidth onClick={props.submitForm}>
                  <Typography header className={classes.whiteTextColor}>
                    Send
                  </Typography>
                  <LongArrowRight className={classes.arrowSvg} />
                </Button>
              </Grid>
              <Grid item xs={12}>
                <FlexBox justifyContent='center'>
                  <Typography header className={classes.whiteTextColor}>
                    Initial Consultation Is Complementary
                  </Typography>
                </FlexBox>
              </Grid>
            </Grid>

          </FlexBox>
        )}
      </Formik>
    </FlexBox>
  );
};

export default ContactPage;