import React from 'react';
import { Card, CardActionArea, CardMedia, CardMediaProps } from '@material-ui/core';

const ImageCard = ({ image, title, children, className, onClick, buttonClass }: CardMediaProps & { onClick?: ((event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void), buttonClass?: string }) => {
  return (
    <Card elevation={2}>
      <CardActionArea onClick={onClick} className={buttonClass}>
        <CardMedia
          component="img"
          className={className}
          image={image}
          title={title}
        />
        {children}
      </CardActionArea>
    </Card>
  );
};

export default ImageCard;