import React from 'react';
import FlexBox from './FlexBox';
import { Link, makeStyles } from '@material-ui/core';
import { ReactComponent as LongArrowRight } from '../media/longArrowRight.svg';
import Typography from './Typography';

const useStyles = makeStyles((theme) => ({
  navRightLeftContainer: {
    margin: `${theme.spacing(2)}px 0`,
    justifyContent: 'space-between',
  },
  labelText: {
    fontSize: '24px',
  },
  linkContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  containerLeft: {
    marginLeft: theme.spacing(2),
    '& > $linkContainer': {
      [theme.breakpoints.down('xs')]: {
        flexDirection: 'column-reverse',
      },
    }
  },
  containerRight: {
    marginRight: theme.spacing(2),
    '& > $linkContainer': {
      [theme.breakpoints.down('xs')]: {
        flexDirection: 'column',
      },
    }
  },
  arrowLeft: {
    fill: theme.palette.primary.main,
    height: '1em',
    transform: 'scale(-1)',
    marginRight: theme.spacing(1),
  },
  arrowRight: {
    fill: theme.palette.primary.main,
    height: '1em',
    marginLeft: theme.spacing(1),
  }
}));

type Nav = {
  label: string,
  href: string,
};
type NavLeftRightProps = {
  left?: Nav,
  right?: Nav,
}
const NavLeftRight = ({ left, right }: NavLeftRightProps) => {
  const classes = useStyles();
  return (
    <FlexBox className={classes.navRightLeftContainer}>
      {left &&
        <FlexBox className={classes.containerLeft}>
          <Link href={left.href} className={classes.linkContainer}>
            <LongArrowRight color='primary' className={classes.arrowLeft} />
            <Typography header className={classes.labelText}>
              {left.label}
            </Typography>
          </Link>
        </FlexBox>}
      {right &&
        <FlexBox className={classes.containerRight}>
          <Link href={right.href} className={classes.linkContainer}>
            <Typography header className={classes.labelText}>
              {right.label}
            </Typography>
            <LongArrowRight color='primary' className={classes.arrowRight} />
          </Link>
        </FlexBox>}
    </FlexBox>
  );
};

export default NavLeftRight;