import React, { ElementType } from 'react';
import { Typography as MuiTypography, TypographyProps, makeStyles } from '@material-ui/core';
import classNames from 'classnames';

const useStyles = makeStyles((theme) => ({
  angeliqueBonques: {
    fontFamily: 'AngelicBonques'
  },

}));

const Typography = ({ className, header, children, component = 'p', ...typProps }: TypographyProps & { header?: true, component?: ElementType }) => {
  const classes = useStyles();
  return (
    <MuiTypography component={component} className={classNames(className, { [classes.angeliqueBonques]: header })} {...typProps}>
      {children}
    </MuiTypography>
  );
};

export default Typography;